/* Global */
[data-theme="claudionsimple"] {
  --main-color: #063c44;
  --second-color: #1cdeb0;
  --third-color: #829da1;
  --fourth-color: #d1d3d4;
  --fifth-color: #808285;
  --sixth-color: #ffffff;

  --primary: var(--main-color);
  --dt-primary-color: var(--main-color);
  --blue-avatar-bg: var(--main-color);
  --blue-avatar-color: var(--main-color);
  --blue: var(--main-color);

  --navbar-bg: var(--main-color);
  --fg-hover-color: var(--fouth-color);
  --card-bg: var(--fourth-color);
  --control-bg: var(--main-color);
  --control-bg-on-pink: var(--second-color);
}

/* Page Background */
[data-theme="claudionsimple"] .page-container,
[data-theme="claudionsimple"] .layout-main-section {
  background-color: var(--main-color);
  border: none !important;
  /* border: 1px solid var(--main-color); */
}

[data-theme="claudionsimple"] .layout-main-section-wrapper {
  border: 1px solid var(--main-color);
}

/* Links */
[data-theme="claudionsimple"] ul li a:hover {
  background-color: var(--second-color);
  color: var(--sixth-color);
}

/* Main Body */
[data-theme="claudionsimple"] body {
  font-size: 14px;
  background-color: var(--fourth-color);
}

[data-theme="claudionsimple"] a {
  color: var(--fifth-color);
  font-weight: 500;
}

[data-theme="claudionsimple"] .small,
small {
  font-size: 95%;
}

[data-theme="claudionsimple"] .h6.uppercase,
h6.uppercase {
  font-weight: 600;
  font-size: 11.5px;
}

/* Navigation Bar */
[data-theme="claudionsimple"] .standard-sidebar-section {
  background-color: var(--fourth-color);
}

[data-theme="claudionsimple"] .standard-sidebar-item.selected {
  background: linear-gradient(to left, var(--main-color), var(--second-color));
}

[data-theme="claudionsimple"] .standard-sidebar-item.selected a {
  color: var(--sixth-color);
}

[data-theme="claudionsimple"] .standard-sidebar-item.selected a svg {
  fill: var(--sixth-color);
}

[data-theme="claudionsimple"] .standard-sidebar-item:hover {
  background-color: var(--second-color);
}

[data-theme="claudionsimple"] .standard-sidebar-item:hover a,
[data-theme="claudionsimple"] .standard-sidebar-item:hover svg {
  color: var(--sixth-color);
  fill: var(--sixth-color);
}

/* Navbar */
[data-theme="claudionsimple"] .navbar {
  background-color: var(--fourth-color);
  border: none !important;
}

[data-theme="claudionsimple"] .navbar-nav > li > a {
  color: var(--sixth-color) !important;
}

[data-theme="claudionsimple"] .navbar-nav > li > a:hover {
  color: var(--fourth-color) !important;
}

[data-theme="claudionsimple"] .navbar-brand:hover {
  color: var(--fourth-color) !important;
}

/* Widgets */
[data-theme="claudionsimple"] .widget {
  background-color: var(--fourth-color);
  border-radius: 10px;
  /* border: none; */
}

[data-theme="claudionsimple"]
  .widget.shortcut-widget-box
  .widget-head
  .widget-title
  i {
  color: var(--third-color);
}

[data-theme="claudionsimple"]
  .widget.shortcut-widget-box
  .widget-head
  .widget-title:hover
  span {
  color: var(--main-color);
}

[data-theme="claudionsimple"] .widget-body .link-item:hover {
  color: var(--sixth-color);
  background: var(--third-color);
}
/* 
[data-theme="claudiontheme3"] .ellipsis {
  color: var(--main-color) !important;
} */

[data-theme="claudionsimple"] .widget.spacer {
  background-color: var(--main-color);
}

[data-theme="claudionsimple"] .h4 {
  color: var(--main-color) !important;
}
/* Sidebar */
[data-theme="claudionsimple"] .desk-sidebar .desk-sidebar-item {
  font-size: 13px;
  font-weight: 600;
  color: var(--fourth-color);
}

[data-theme="claudionsimple"] .layout-side-section {
  background-color: var(--fourth-color);
  font-size: 14px;
  border-radius: 10px;
}

[data-theme="claudionsimple"] .uppercase {
  color: var(--third-color);
}

/* Dropdowns */
[data-theme="claudionsimple"] .dropdown-menu > li > a {
  font-size: 13px;
  color: var(--fifth-color);
  font-weight: 500;
}

[data-theme="claudionsimple"] .awesomplete > ul > li:hover {
  background: linear-gradient(to left, var(--main-color), var(--second-color));
}

[data-theme="claudionsimple"] .awesomplete > ul > li:hover a {
  color: var(--sixth-color);
}

/* Page Heading */
[data-theme="claudionsimple"] .page-head {
  background-color: var(--main-color);
  border-bottom: none !important;
  /* box-shadow: 0 1px 10px 1px rgba(115, 108, 203, 0.1); */
}

[data-theme="claudionsimple"] .page-title .title-text {
  color: var(--second-color);
  font-size: 22px;
}

[data-theme="claudionsimple"] .widget .widget-head .widget-title,
[data-theme="claudionsimple"] .section-head,
[data-theme="claudionsimple"] .comment-input-header {
  font-size: 14px;
  color: var(--main-color);
}

/* Buttons */
[data-theme="claudionsimple"] .btn-primary {
  background-color: var(--main-color);
  border-color: var(--main-color);
}

/* [data-theme="claudiontheme3"] .sidebar-toggle-btn {
  color: var(--fourth-color);
} */
[data-theme="claudionsimple"] .btn-default:hover,
[data-theme="claudionsimple"] .btn-default:focus,
.btn-default.focus,
[data-theme="claudionsimple"] .btn-default:active,
.btn-default.active,
[data-theme="claudionsimple"] .open > .dropdown-toggle.btn-default {
  background-color: var(--third-color) !important;
  color: var(--sixth-color) !important;
}

[data-theme="claudionsimple"] .btn-secondary {
  background-color: var(--main-color);
  color: var(--sixth-color);
}

[data-theme="claudionsimple"] .btn-secondary:hover {
  background-color: var(--third-color);
  color: var(--sixth-color);
}

/* Indicators */
[data-theme="claudionsimple"] .indicator-right.blue:after,
.indicator.blue:before {
  background: var(--third-color);
}

/* Other Components */
[data-theme="claudionsimple"] input[type="checkbox"]:checked:before {
  color: var(--third-color);
}

[data-theme="claudionsimple"] input[type="checkbox"]:checked {
  background: var(--main-color);
}

[data-theme="claudionsimple"] #page-Workspaces .page-head {
  background: var(--main-color);
}
